import React from 'react';
import PropTypes from 'prop-types';
import Select from 'react-select';

const StaticShopSelector = ({ isLoading, isError, shops, onChange }) => {
  if (isError) {
    return <Select isRtl placeholder={'!'} options={[]} isDisabled={true} />;
  }

  shops =
    shops && shops.length
      ? shops.map(shop => {
          return { value: shop.id, label: shop.name };
        })
      : [];

  return (
    <Select
      isRtl
      placeholder={''}
      options={shops}
      isLoading={isLoading}
      isDisabled={isLoading}
      onChange={e => onChange(e.value)}
      styles={{
        container: (p, s) => ({
          padding: '0px 15px',
        }),
        menu: (p, s) => ({
          'z-index': 100,
        }),
      }}
    />
  );
};

StaticShopSelector.propTypes = {
  isLoading: PropTypes.bool,
  isError: PropTypes.bool,
  shops: PropTypes.array,
  onChange: PropTypes.func,
};

StaticShopSelector.defaultProps = {
  isLoading: false,
  isError: false,
  shops: [],
  onChange: () => undefined,
};

export { StaticShopSelector };
